import {
	mapState
} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			cartList: [],
			orderTotalPrice:0,//总价格
			expressPrice:0,//运费
			goodsId:0,//商品id
			goodsSkuId:0,//规则
			tempNumber:0,
			token:'',
			
		};
	},
	async onLoad(){
		let self = this;
		self.loadData();
		//获取token
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				console.log(res.data);
				self.token = res.data.token;
			}
		});
		
	},
	onShow() { //返回显示页面状态函数
		//，来进行局部刷新
		this.loadData();
	},
	computed:{
		...mapState(['hasLogin']),
		
	},
	
	methods: {
		//请求数据
		async loadData(){
			let self = this;
			self.$axios(self.$baseUrl.shopCartList)
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					self.cartList =  result.goods_list;
					self.cartList = self.cartList.map(item=>{
						item.checked = false;
						return item;
					});
					self.orderTotalPrice = result.order_pay_price;
					self.expressPrice = result.express_price;
					self.empty = result.goods_list.length >0?false:true;
				}
				
			},(error) =>{
				console.log(error)
			})
			
			
		},
		 //选中状态处理
		check(type, index){
			let self = this;
			let tabIndex = index;
			if(type == 'item'){
				self.allChecked = 0;
				self.cartList[index].checked = !self.cartList[index].checked;
				let passed = self.cartList.every((item)=>{
					return item.checked
				});
				if(passed){
					self.allChecked = true;
				}else{
					self.allChecked = false;
				}
				
			}else{
				const checked = !self.allChecked
				const list = self.cartList;
				list.forEach(item=>{
					item.checked = checked;
				})
				self.allChecked = checked;
			}
			
		},
		navToLogin(){
			uni.navigateTo({
				url: '/pages/public/login'
			})
		},
		
		//数量
		numberChange(data){
			this.cartList[data.index].number = data.number;
			
		},
		//删除
		deleteCartItem(todo){
			let self = this;
			let goodsId = todo.goods_id;
			let goodsSkuId = todo.goods_sku_id;
			uni.showModal({
				title: '删除提示',
				content: '你将删除这个商品',
				success: (res)=>{
					if (res.confirm) {
						self.$axios(self.$baseUrl.delCart,{
							goods_id:goodsId,
							goods_sku_id:goodsSkuId
						})
						.then(res =>{
							
							if(res.data.code == 1){
								uni.showToast({title:'删除成功',icon:'success'});
								//进行局部刷新
								this.loadData();
							}
							
						},(error) =>{
							console.log(error)
						})
						
					} else if (res.cancel) {
						
					}
				}
			});
			
			uni.hideLoading();
		},
		//清空
		clearCart(){
			let self = this;
			uni.showModal({
				title: '清空提示',
				content: '你要清空购物车里面的商品吗？',
				success: (res)=>{
					if (res.confirm) {
						self.$axios(self.$baseUrl.clearAllCart,{
							token:self.token
						})
						.then(res =>{
							
							if(res.data.code == 1){
								uni.showToast({title:'删除成功',icon:'success'});
								//进行局部刷新
								this.loadData();
							}
							
						},(error) =>{
							console.log(error)
						})
						
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		
		//结算
		createOrder(){
			let self = this;
			let cartIdArr= [];
			let cartId = '';
			self.cartList.forEach((item,index)=>{
				if(item.checked){
					cartIdArr.push(item.goods_id+'_'+item.goods_sku_id);
				}
				
			})
			cartIdArr.forEach((item,index)=>{
				if(cartIdArr.length-1 > index){
					cartId += item+','
				}else{
					cartId += item;
				}
				
			})
			if(cartIdArr.length == 0){
				uni.showToast({title:'请先选择商品',icon:'none'});
			}else{
			
				//跳转到订单列表
				uni.navigateTo({
					url: `/pages/cart/cartdetails?cart_id=${cartId}`
				})	
			}
			
			
		},
	     
		//商品详情
		commodityDetails(id){
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		}
			
	},
}
